import { useBlockProps, useInnerBlocksProps } from '@wordpress/block-editor';
import '../scss/editor.scss';
import previewImgDis from '../../../assets/img/preview-discover-youth.jpg';

export default function Edit({ attributes }) {
	const { preview } = attributes
	const blockProps = useBlockProps({
		className: `ecojunior-content`,
	});

	const TEMPLATE = [
		['core/group', {
			metadata: {
				name: 'Conteneur Ecojunior heading'
			},
			className: 'ecojunior-content--heading'
		},
			[
				['core/heading', {
					level: 2,
					content: 'Télécharger les anciens numéros'
				}],
				['citeo-semantic/horizontal-slider', {
					metadata: {
						name: 'Carousel d\'années de publication'
					},
					className: 'dynamic-year-slider',
					isDraggable: false,
					allowedBlocks: []
				},
					[]
				]
			]
		],
		['citeo-semantic/list-accordeon', {
			metadata: {
				name: "Accordéons de magazines"
			},
			className: 'ecojunior-content--accordeon',
			multipleActive: false,
			allowedBlocks: []
		},
			[]
		],
		['core/group', {
			metadata: {
				name: 'Conteneur download Ecojunior footer'
			},
			className: 'ecojunior-content--footer',
		}, 
			[
				['core/paragraph', {
					typo: 'Body-2',
					content: 'accessible à tous'
				}],
				['core/heading', {
					level: 3,
					content: 'Recevez Eco Junior <br>en version numérique PDF'
				}],
				['core/paragraph', {
					typo: 'Body-2',
					content: 'Abonnez-vous à Ecojunior pour recevoir les prochains numéros directement chez vous !'
				}],
				['core/buttons', {
					className: 'discover-button-wrapper',
					templateLock: false,
					layout: {
						justifyContent: 'center'
					}
				}, 
					[
						['core/button', {
							className: 'is-style-primary',
							text: 'S\'abonner pour 2025/2026'
						}]
					]
				]
			]
		]
	];

	const ALLOWED_BLOCKS = [];

	const innerBlocksProps = useInnerBlocksProps(blockProps, {
		template: TEMPLATE,
		allowedBlocks: ALLOWED_BLOCKS
	});
	
	if (preview) {
			return (
				<>
					<img src={previewImgDis} alt="Preview" />
				</>
			);
		}

	return (
		<section {...innerBlocksProps} />
	);
}
